body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  -webkit-user-select: none;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  -ms-user-select: none;
      user-select: none;
}

.App {
  text-align: left;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#map {
  height: 100vh;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#rowHeader {
  display: inline-block;
  padding-left: 5px;
}

label {
  display: block;
  width: 80vw;
  margin: auto;
  margin-left: 10px;
  margin-bottom: -2px;
}

#restaurantRow {
  display: block;
  margin: auto;
  border: 2px solid black;
  margin-bottom: -2px;
  width: 80vw;
}

#autocomplete {
margin: 5px 0 20px 10px;
}

#rowCheckbox {
  display: inline-block;
  margin-left: 10px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.been-here {
  display: block;
  margin-top: -41px;
  margin-bottom: 0;
  text-align: center;
  color: rgb(122, 8, 8);
  font-size: 30px;
}

